.releaseAction-root {
    width: 100vw;
    height: 100vh;
    .sup-navbar-center{
      font-size: 1rem;
    }

    .releaseAction-title {
        padding: 10px 20px;
        display: flex;
        justify-content: space-between;
        position: relative;
        border-bottom: 1px solid #ccc;

        & > div:nth-of-type(1) {
            height: 40px;
            line-height: 48px;
            margin-left: 20px;

            &::before {
                display: block;
                content: '';
                width: 10px;
                height: 55%;
                background: #328af7;
                position: absolute;
                top: 25%;
                left: 2%;
            }
        }

        .arrow {
            width: 40px;
            height: 40px;
            border: 1px solid #868686;
            border-radius: 50%;
            position: relative;

            &::after {
                display: block;
                content: 'ã€‰';
                width: 100%;
                height: 100%;
                color: #868686;
                position: absolute;
                top: 35%;
                left: 50%;
                transform: translateX(-50%) rotate3d(0, 0, 1, 90deg);
            }
        }

        .notAll {
            .arrow();
            &::after {
                top: -35%;
                transform: translateX(-50%) rotate3d(0, 0, 1, -90deg);
            }
        }
    }

    .releaseAction-content {
        width: 100%;
        height: 100%;
        background: #fff;
        position: relative;
        padding: 0 20px;
        box-sizing: border-box;

        & > div {
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid #ccc;
            padding: 10px 0;

            & > div:nth-of-type(1) {
                min-width: 20%;
                color: rgb(149, 146, 146);
            }

        }
    }
    .releaseAction-hidden{
        display: none;
    }
}
